"use client";

import { useTheme } from "next-themes";
import { useEffect, useState } from "react";

export default function ThemeToggle() {
    const { theme, setTheme } = useTheme();
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
    }, []);

    if (!mounted) return null;

    const toggleTheme = () => {
        setTheme(theme === "dark" ? "light" : "dark");
    };

    return (
        <button
            onClick={toggleTheme}
            className="p-2 rounded-full bg-gray-200 dark:bg-zinc-700 hover:bg-gray-300 dark:hover:bg-zinc-600 transition-all shadow-sm hover:shadow-md"
            aria-label="Toggle theme"
            title={`Switch to ${theme === "dark" ? "light" : "dark"} mode`}
        >
            {theme === "dark" ? (
                // üåô Show moon in dark mode ‚Üí switch to light
                <svg
                    className="h-5 w-5 text-gray-200"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                >
                    <path d="M17.293 13.293A8 8 0 016.707 2.707a8.001 8.001 0 1010.586 10.586z" />
                </svg>
            ) : (
                // ‚òÄÔ∏è Show sun in light mode ‚Üí switch to dark
                <svg
                    className="h-5 w-5 text-yellow-500"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                >
                    <path
                        fillRule="evenodd"
                        d="M10 2a1 1 0 011 1v1a1 1 0 11-2 0V3a1 1 0 011-1zm4.22 2.22a1 1 0 011.415 0l.708.707a1 1 0 11-1.415 1.415l-.708-.707a1 1 0 010-1.415zM18 9a1 1 0 100 2h1a1 1 0 100-2h-1zm-2.657 6.657a1 1 0 010 1.415l-.707.708a1 1 0 01-1.415-1.415l.707-.708a1 1 0 011.415 0zM10 16a1 1 0 011 1v1a1 1 0 11-2 0v-1a1 1 0 011-1zm-6.364-.364a1 1 0 011.415 0l.707.707a1 1 0 11-1.414 1.415l-.708-.707a1 1 0 010-1.415zM4 9a1 1 0 100 2H3a1 1 0 100-2h1zm2.05-4.95a1 1 0 010 1.415l-.707.707A1 1 0 014.93 4.757l.708-.708a1 1 0 011.414 0z"
                        clipRule="evenodd"
                    />
                    <path d="M10 14a4 4 0 100-8 4 4 0 000 8z" />
                </svg>
            )}
        </button>
    );
}
